@import '../../styles/colors';

.input-wrapper {
    input {
        padding:16px;
        border-radius: 2px;
        font-family: inherit;
        font-size: inherit;
        border: 0;
        margin: 8px;
        max-width: 600px;
        width: 100%;
        box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2);
        
        &:focus {
            outline: none;
            border-color: $black;
        }
    }

    .input-error {
        box-shadow: 0 1px 2px 0 rgba(233, 20, 20, 0.76);
    }
}


    
 