@import '../styles/colors';

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: -100px;
  font-family: 'Open Sans'; 
  font-size: 15px;
  font-weight: normal;
  max-width: 600px;
  width: 100%;
}

.buttons {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 130px;
  width: inherit;
}

.forms {
  margin: 0 8px;
  min-height: 200px;
  width: inherit;
}

.error {
  color: $red;
  font-family: inherit;
  font-size: inherit;
}
